import Topbar from '../../components/topBar/topbarx.vue'
export default {
	data() {
		return {
			title: "兑换记录",
			recording: [], // 兑换记录
			// isLook: [], //是否显示红点
			isLoding: false,
			current: 1,
			size: 10, 
		};
	},
	onLoad(){
		this.getData()
	},
	onUnload(){
		uni.hideLoading()
	},
	methods: {
		getData(){
			let _this = this
			let userinfo = this.$store.state.userInfo
			uni.request({
				url: this.$http + '/api/goods/exchangeList',
				method: 'POST',
				data:{
					token: userinfo.token,
					page: _this.current,
					size: _this.size
				},
				success(res){
					console.log('兑换记录返回数据', res)
					if(res.data.code == 1){
						if(_this.recording.length ==0){
							_this.recording = res.data.data
						}else{
							let arr = _this.recording
							let newArr = arr.concat(res.data.data)
							_this.recording = newArr
							if(res.data.data.length == 0){
								_this.isLoding = false
							}
						}
						
						_this.isLoding = false
					}
				}
			})
		},
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
		// 返回上级目录
		goBack(){
			uni.navigateTo({
				url: '../pointsmall/pointsmall'
			})
		},
		// 点击过的项 将该项的索引存放到数组中
		addIndex(item, index){
			let _this = this
			if(item.is_look == 1) return
			
			let userinfo = this.$store.state.userInfo
			uni.request({
				url: this.$http + '/api/goods/exchangeLook',
				method: 'POST',
				data: {
					token: userinfo.token,
					id: item.id
				},
				success(res){
					console.log('红点数据', res)
					if(res.data.code == 1){
						_this.recording[index].is_look = 1
						console.log(_this.recording[index])
						// _this.getData()
					}
					
				}
			})
		},
		onReachBottom(){//触底事件
			this.current++
			this.isLoding = true
			this.getData()
			 console.log('页面到底了', this.current)
		},
	},
	components: {
		Topbar
	}
}
